import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/shared/services/login.service';
import { PreviousRouteService } from 'src/app/shared/services/previous-route.service';
import { UserRoleService } from 'src/app/shared/services/user-role.service';

@Component({
  selector: 'app-asso-session-expire-dialog',
  templateUrl: './asso-session-expire-dialog.component.html',
  styleUrls: ['./asso-session-expire-dialog.component.scss']
})
export class AssoSessionExpireDialogComponent implements OnInit {

  applicationHasLicense = true;
  licenseOnLogin: boolean = false;
  message: string;

  constructor(
    private _userRoleService: UserRoleService,
    private _authService : AuthService,
    private _prevURL: PreviousRouteService,
  ) { }

  ngOnInit() {
    try {
      let accessPermissions = this._userRoleService.getGlobalAccountPermissions();
      this.applicationHasLicense = accessPermissions.applicationHasLicense;
    } catch (error) {
      this.applicationHasLicense = true;
    }
    const isLoggedIn = localStorage.getItem("isLoggedIn");
    if(!isLoggedIn && !this.licenseOnLogin){
      this._authService.logout(true);
    } 
    this.generateMessageForUser(this._prevURL.getPreviousUrl());

  }

  generateMessageForUser(prevUrl){
    let changedMessage;
    if(prevUrl === "" || prevUrl === "/"){
      prevUrl = " current page";
    }

    if(prevUrl.split("/").length > 2){
      let splitedPrevUrl = prevUrl.split("/");
      prevUrl = splitedPrevUrl[1] + " " + splitedPrevUrl[2];
    }

    changedMessage = prevUrl.replace(/-/g, ' ');
    changedMessage = changedMessage.replace(/[/]/g, ' ');
    this.message = changedMessage;
  }

  redirectToLoginPage(){
    this._authService.returnToLoginPage();
  }

}
